# Content
--------------------------------------------------------------*/
.day:not(:last-child) .card {
  border-bottom: 0;
  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0; }

.day:not(:first-child) .card {
  border-top-left-radius: 0;
  border-top-right-radius: 0; }

.card-body.active {
  background-color: #fffff0; }

input {
  border: none;
  font-weight: 300;
  width: 100%;
  background-color: transparent; }

@media (max-width: 1199px) {
  .display-5 {
    font-size: calc(1.2rem + 2.1vw); } }

div.recipe .form-group,
div.recipe .input-group {
  margin-bottom: .25rem; }
  div.recipe .form-group span.input-group-text,
  div.recipe .input-group span.input-group-text {
    cursor: pointer; }

div.recipe.show dl {
  margin-bottom: 0; }

div.recipe.show dt, div.recipe.show dd {
  display: inline; }

